"use client";

import { motion } from "framer-motion";
import { Play, Info, Plus } from "lucide-react";
import Image from "next/image";
import { useEffect, useState } from "react";
import { getTrendingMovies } from "@/api/tmdb";
import Link from "next/link";

export function Hero() {
    const [movie, setMovie] = useState<any>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function loadHero() {
            try {
                const data = await getTrendingMovies("day");
                if (data && data.results && data.results.length > 0) {
                    // Pick random from top 5 to keep it fresh
                    const random = data.results[Math.floor(Math.random() * 5)];
                    setMovie(random);
                } else {
                    throw new Error("No data");
                }
            } catch (e) {
                // Fallback to Oppenheimer if API fails
                setMovie({
                    id: 872585,
                    title: "Oppenheimer",
                    tagline: "The world uses change as a weapon.",
                    overview: "The story of American scientist J. Robert Oppenheimer and his role in the development of the atomic bomb.",
                    backdrop_path: "/fm6KqXpk3M2HVveHwCrBSSBaB0V.jpg",
                    genres: [18, 36],
                    release_date: "2023-07-19",
                    vote_average: 8.2
                });
            } finally {
                setLoading(false);
            }
        }
        loadHero();
    }, []);

    if (loading) return <div className="h-[85vh] w-full bg-bg-main animate-pulse" />;
    if (!movie) return null;

    return (
        <section className="relative h-[85vh] w-full overflow-hidden">
            {/* Background Image with Parallax */}
            <div className="absolute inset-0">
                {movie.backdrop_path && (
                    <Image
                        src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                        alt={movie.title}
                        fill
                        priority
                        className="object-cover object-center"
                    />
                )}
                {/* Grain Overlay */}
                <div className="film-grain absolute inset-0 z-10" />

                {/* Gradients */}
                <div className="absolute inset-0 bg-gradient-to-r from-bg-main via-bg-main/50 to-transparent z-10 opacity-90" />
                <div className="absolute inset-0 bg-gradient-to-t from-bg-main via-transparent to-transparent z-10" />
            </div>

            {/* Content */}
            <div className="relative z-20 h-full container mx-auto px-6 lg:px-20 flex flex-col justify-center">
                <motion.div
                    initial={{ opacity: 0, y: 30 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.8, delay: 0.2 }}
                    className="max-w-2xl"
                >
                    {/* Metadata Badges */}
                    <div className="flex items-center gap-3 mb-4">
                        <span className="px-2 py-1 bg-white/10 backdrop-blur-md rounded text-xs font-medium text-white border border-white/10">
                            TMDB {movie.vote_average?.toFixed(1)}
                        </span>
                        <span className="text-text-secondary text-sm font-medium">
                            {movie.release_date?.split("-")[0]}
                        </span>
                    </div>

                    {/* Title */}
                    <h1 className="font-display text-5xl md:text-7xl lg:text-8xl font-bold text-white leading-[0.9] mb-4 drop-shadow-2xl">
                        {movie.title}
                    </h1>

                    {/* Description */}
                    <p className="text-text-muted text-base md:text-lg line-clamp-3 mb-8 max-w-lg leading-relaxed">
                        {movie.overview}
                    </p>

                    {/* Buttons */}
                    <div className="flex items-center gap-4">
                        <button className="flex items-center gap-2 px-8 py-3.5 bg-accent-primary hover:bg-accent-primary/90 text-white font-bold rounded-lg transition-all transform hover:scale-105 shadow-[0_0_20px_rgba(229,9,20,0.4)]">
                            <Play fill="currentColor" size={20} />
                            Watch Trailer
                        </button>

                        <Link href={`/movie/${movie.id}`}>
                            <button className="flex items-center gap-2 px-6 py-3.5 bg-white/10 hover:bg-white/20 border border-white/10 backdrop-blur-md text-white font-medium rounded-lg transition-all">
                                <Info size={20} />
                                More Info
                            </button>
                        </Link>

                        <button className="p-3.5 bg-white/5 hover:bg-white/10 border border-white/10 rounded-lg text-text-secondary hover:text-white transition-all">
                            <Plus size={20} />
                        </button>
                    </div>
                </motion.div>
            </div>
        </section>
    );
}
